import { interfaces } from 'inversify'
import { CMS_ADMIN_ENV, cmsAdminServiceModule, DubSyncExtname } from '@flowda-projects/cms-admin-services'
import { PrismaClient } from '@prisma/client-cms_admin'
import {
  COSSymbol,
  flowdaSharedModule,
  FlowdaTrpcClientSymbol,
  PrismaClientSymbol,
} from '@flowda-projects/flowda-shared'
import { flowdaSharedNodeModule } from '@flowda-projects/flowda-shared-node'
import type { CreateTRPCProxyClient } from '@trpc/client'
import type { AppRouter } from '@flowda-projects/flowda-services-trpc-server'
import { trpc } from './trpc/trpc'

const COS = require('cos-nodejs-sdk-v5')

console.log('---------- ENV --------------')
console.log('PROXY', CMS_ADMIN_ENV.PROXY)
console.log('FLOWDA_URL', CMS_ADMIN_ENV.FLOWDA_URL)
console.log('---------- ENV --------------')

export function loadModule(container: interfaces.Container) {
  const prisma = new PrismaClient({
    log: ['query', 'info', 'warn', 'error'],
  }).$extends({
    name: DubSyncExtname,
    query: {
      site: {
        async $allOperations({ model, operation, args, query }) {
          if (operation === 'update') {
            if (typeof args.data.name === 'string' && args.where.id != null) {
              const prevRet = await prisma.site.findUniqueOrThrow({
                where: {
                  id: args.where.id,
                },
              })
              if (prevRet.projectId != null) {
                console.log(`[prisma.$extend] ${model}.${operation}(${JSON.stringify(args, null, 2)})`)
                return prisma.project.update({
                  where: {
                    id: prevRet.projectId,
                  },
                  data: {
                    name: args.data.name,
                  },
                })
              }
            }
          }
          return query(args)
        },
      },
      project: {
        async $allOperations({ model, operation, args, query }) {
          if (operation === 'update') {
            if (typeof args.where.slug === 'string') {
              const projectRet = await this.prisma.project.findUniqueOrThrow({
                where: {
                  slug: args.where.slug,
                },
              })
              console.log(`[prisma.$extend] ${model}.${operation}(${JSON.stringify(args, null, 2)})`)
              const ret = await this.prisma.site.update({
                where: {
                  projectId: projectRet.id,
                },
                data: {
                  name: args.data.name,
                },
              })
              return ret
            }
          }
          return query(args)
        },
      },
    },
  }) as PrismaClient
  container.bind<PrismaClient>(PrismaClientSymbol).toConstantValue(prisma)
  container.bind<CreateTRPCProxyClient<AppRouter>>(FlowdaTrpcClientSymbol).toConstantValue(trpc)

  container.bind<typeof COS>(COSSymbol).toDynamicValue(() => {
    return new COS({
      SecretId: CMS_ADMIN_ENV.COS_ID,
      SecretKey: CMS_ADMIN_ENV.COS_KEY,
    })
  })
  container.load(flowdaSharedModule)
  container.load(flowdaSharedNodeModule)
  container.load(cmsAdminServiceModule)
}
