import { inject, injectable } from 'inversify'
import { FlowdaTrpcClientSymbol, PrismaClientSymbol } from '@flowda-projects/flowda-shared'
import { LoggerService } from '@nestjs/common'
import * as db from '@prisma/client-cms_admin'
import type { CreateTRPCProxyClient } from '@trpc/client'
import { AppRouter } from '@flowda-projects/flowda-services-trpc-server'
import {
  alreadyInTeamSchemaDto,
  findProjectOwnerSchemaDto,
  findUserByEmailSchemaDto,
  findUserByIdSchemaDto,
  queryLinksSchemaDto,
  queryProjectUsersSchemaDto,
  updateProjectSchemaDto,
  updateUserInfoSchemaDto,
} from './dub.dto'

@injectable()
export class DubService {
  private readonly logger

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject(FlowdaTrpcClientSymbol) private flowdaTrpc: CreateTRPCProxyClient<AppRouter>,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(DubService.name)
  }

  findUserByEmail(dto: findUserByEmailSchemaDto) {
    return this.flowdaTrpc.user.findUnique.query({
      email: dto.email,
    })
  }

  updateUserInfo(dto: updateUserInfoSchemaDto) {
    return this.flowdaTrpc.user.updateUserInfo.mutate({
      userId: dto.userId,
      username: dto.name,
      email: dto.email,
      image: dto.image,
    })
  }

  findUserById(dto: findUserByIdSchemaDto) {
    return this.flowdaTrpc.user.findUnique.query({
      id: dto.id,
    })
  }

  async findProjectOwner(dto: findProjectOwnerSchemaDto) {
    const projectUserRet = await this.prisma.projectUsers.findFirst({
      where: {
        role: 'owner',
        project: {
          slug: dto.slug,
        },
      },
    })
    if (projectUserRet?.userId != null) {
      const userRet = await this.flowdaTrpc.user.findUnique.query({ id: Number(projectUserRet.userId) })
      if (userRet) {
        return {
          email: userRet.email,
        }
      } else {
        return null
      }
    } else {
      return null
    }
  }

  async queryProjectUsers(dto: queryProjectUsersSchemaDto) {
    const projectUsersRet = await this.prisma.projectUsers.findMany({
      where: {
        projectId: dto.projectId,
      },
      select: {
        userId: true,
      },
    })
    const userIds = projectUsersRet.filter(i => i.userId != null).map(i => Number(i.userId))
    const usersRet = await this.flowdaTrpc.user.findMany.query({ userIds: userIds })
    return projectUsersRet.map(ret => {
      return Object.assign(ret, {
        user: usersRet.find(u => u.id === Number(ret.userId)),
      })
    })
  }

  async updateProject(dto: updateProjectSchemaDto) {
    this.logger.debug!(`[updateProject] invoked, ${JSON.stringify(dto, null, 2)}`)
    const ret = await this.prisma.project.update({
      where: {
        slug: dto.previousSlug,
      },
      data: {
        ...(dto.name && { name: dto.name }),
        ...(dto.slug && { slug: dto.slug }),
      },
    })
    return ret
  }

  async queryLinks(dto: queryLinksSchemaDto) {
    const siteRet = await this.prisma.site.findUniqueOrThrow({
      where: {
        projectId: dto.projectId,
      },
    })
    return siteRet
  }

  async alreadyInTeam(dto: alreadyInTeamSchemaDto) {
    const projectUsers = await this.queryProjectUsers({
      projectId: dto.projectId,
    })
    return projectUsers.filter(pu => {
      if (pu.user) {
        return pu.user.email === dto.email
      }
      return false
    })[0]
  }
}
