import { createZodDto } from 'nestjs-zod'
import { z } from 'zod'

export const findUserByEmailSchema = z.object({
  email: z.string(),
})

export class findUserByEmailSchemaDto extends createZodDto(findUserByEmailSchema) {}

export const findUserByIdSchema = z.object({
  id: z.number(),
})

export class findUserByIdSchemaDto extends createZodDto(findUserByIdSchema) {}

export const findProjectOwnerSchema = z.object({
  slug: z.string(),
})

export class findProjectOwnerSchemaDto extends createZodDto(findProjectOwnerSchema) {}

export const queryProjectUsersSchema = z.object({
  projectId: z.string(),
})

export class queryProjectUsersSchemaDto extends createZodDto(queryProjectUsersSchema) {}

export const updateProjectSchema = z.object({
  previousSlug: z.string(),
  slug: z.string().optional(),
  name: z.string().optional(),
})

export class updateProjectSchemaDto extends createZodDto(updateProjectSchema) {}

export const queryLinksSchema = z.object({
  projectId: z.string(),
})

export class queryLinksSchemaDto extends createZodDto(queryLinksSchema) {}

export const updateUserInfoSchema = z.object({
  userId: z.number(),
  email: z.string().optional(),
  name: z.string().optional(),
  image: z.string().optional(),
})

export class updateUserInfoSchemaDto extends createZodDto(updateUserInfoSchema) {}

export const alreadyInTeamSchema = z.object({
  projectId: z.string(),
  email: z.string(),
})

export class alreadyInTeamSchemaDto extends createZodDto(alreadyInTeamSchema) {}
