import { z } from 'zod'
import { createZodDto } from 'nestjs-zod'
import { IsInt, IsString } from 'class-validator'
import { Type } from 'class-transformer'

const GetTemplateDataDefSchema = z.object({
  siteId: z.number(),
})

export class GetTemplateDataDefSchemaDto extends createZodDto(GetTemplateDataDefSchema) {}

const generateSiteJobSchema = z.object({
  take: z.number().optional(),
})

export class GenerateSiteJobSchemaDto extends createZodDto(generateSiteJobSchema) {}

const GenerateSiteSchema = z.object({
  templateId: z.number(),
  customerId: z.number(),
})

export class GenerateSiteSchemaDto extends createZodDto(GenerateSiteSchema) {}

const SubmitPreviewSiteSchema = z.object({
  siteId: z.number(),
  slotData: z.any(),
})

export class SubmitPreviewSiteSchemaDto extends createZodDto(SubmitPreviewSiteSchema) {}

const GeneratePartialSlotDataSchema = z.object({
  siteId: z.number(),
  path: z.string(),
})

export class GeneratePartialSlotDataSchemaDto extends createZodDto(GeneratePartialSlotDataSchema) {}

export const DubSyncExtname = 'dub-sync'

// https://dev.to/avantar/validating-numeric-query-parameters-in-nestjs-gk9
export class GetRandomImagesQuery {
  @IsInt()
  @Type(() => Number)
  count!: number

  @IsString()
  tag!: string
}
