import { inject, injectable } from 'inversify'
import { COSSymbol, DataService, DataServiceSymbol, PrismaClientSymbol } from '@flowda-projects/flowda-shared'
import { LoggerService } from '@nestjs/common'
import * as db from '@prisma/client-cms_admin'
import { generateSchema as zodToOpenAPI } from '@anatine/zod-openapi'
import { CustomerDataDef } from '../lib/dynamic-schema'
import * as _ from 'radash'
import type * as COS from 'cos-nodejs-sdk-v5'
import * as Handlebars from 'handlebars'
import { HTMLElement, parse } from 'node-html-parser'
import {
  GeneratePartialSlotDataSchemaDto,
  GenerateSiteJobSchemaDto,
  GenerateSiteSchemaDto,
  GetRandomImagesQuery,
  GetTemplateDataDefSchemaDto,
  SubmitPreviewSiteSchemaDto,
} from './custom.dto' // eslint-disable-next-line @typescript-eslint/no-var-requires

const BUCKET = 'assets-1306445775'

@injectable()
export class CustomService {
  private readonly logger

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject(DataServiceSymbol) private data: DataService,
    @inject(COSSymbol) private cos: COS,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(CustomService.name)
  }

  async generateSite(dto: GenerateSiteSchemaDto): Promise<db.Site> {
    this.logger.debug!(`[generateSite] ${JSON.stringify(dto, null, 2)}`)
    const cusRet = await this.prisma.customer.findUniqueOrThrow({
      where: {
        id: dto.customerId,
      },
    })
    const companyInfo: any = cusRet.extendData
    this.logger.debug!(`[generateSite] start invoke generateData`)
    if (companyInfo.description == null) {
      const msg = `${cusRet.name}'s description is null`
      this.logger.error(msg)
      throw new Error(msg)
    }
    // eslint-disable-next-line @typescript-eslint/no-var-requires
    const { generateData } = require('ai-gen-utils')
    const data = await generateData(companyInfo)
    this.logger.debug!(`[generateSite] end invoke generateData`)
    const siteRet = await this.prisma.site.findFirst({
      where: {
        customerId: dto.customerId,
        siteTemplateId: dto.templateId,
        isDeleted: false,
      },
    })
    let ret
    if (siteRet) {
      ret = await this.prisma.site.update({
        where: {
          id: siteRet.id,
        },
        data: {
          name: cusRet.name + _.uid(4),
          siteTemplateId: dto.templateId,
          customerId: dto.customerId,
          slotData: data,
        },
      })
    } else {
      ret = await this.prisma.site.create({
        data: {
          name: cusRet.name + _.uid(4),
          siteTemplateId: dto.templateId,
          customerId: dto.customerId,
          slotData: data,
        },
      })
    }
    // const htmlRet = ejs.render(tplRet.template, dto.defData)
    this.logger.debug!(`[generateSite] ret ${JSON.stringify(ret, null, 2)}`)
    return ret
  }

  async generatePartialSlotData(dto: GeneratePartialSlotDataSchemaDto) {
    this.logger.debug!(`[generatePartialSlotData] ${JSON.stringify(dto, null, 2)}`)
    const siteRet = await this.prisma.site.findUniqueOrThrow({
      where: {
        id: dto.siteId,
      },
      include: {
        customer: true,
      },
    })
    this.logger.debug!(`[generatePartialSlotData] start invoke generateDataPartial`)
    // eslint-disable-next-line @typescript-eslint/no-var-requires
    const { generateDataPartial } = require('ai-gen-utils')
    const data = await generateDataPartial(siteRet.customer.extendData, siteRet.slotData, dto.path)
    this.logger.debug!(`[generatePartialSlotData] end invoke generateDataPartial`)
    // const htmlRet = ejs.render(tplRet.template, dto.defData)
    return data
  }

  async previewSite(siteId: string) {
    if (isNaN(parseInt(siteId))) {
      throw new Error(`[previewSite] siteId is illegal, ${siteId}`)
    }

    const siteRet = await this.prisma.site.findUniqueOrThrow({
      where: {
        id: parseInt(siteId),
      },
      include: {
        siteTemplate: true,
      },
    })
    const { prefix, compiledTemplate } = await this.getTemplate(siteRet.siteTemplate.template)
    const generatedHTML = compiledTemplate(Object.assign(siteRet.slotData as any, { id: siteRet.id }))
    const root = parse(generatedHTML)
    this.updateScriptStyle(root, prefix)
    // 添加一些 iframe 通信代码
    root.querySelector('head')!.insertAdjacentHTML(
      'beforeend',
      `
    <style>
.blink {
  animation: blink-animation 1s linear 3;
}
@keyframes blink-animation {
  0% { background-color: #f9c6d8; }
  50% { background-color: white; }
  100% { background-color: #f9c6d8; }
}
    </style>
    `,
    )
    root.querySelector('body')!.insertAdjacentHTML(
      'beforeend',
      `
    <script>
      console.log('inject iframe message proxy')
      window.addEventListener('message', function (e) {
        console.log('from parent', e.data)
        try {
            const { path, value } = JSON.parse(e.data)
            const ele = document.querySelectorAll('[data-slot="' + path + '"]')[0]
            ele.innerText = value
            ele.scrollIntoView({ behavior: 'smooth' });
            ele.classList.add('blink');
            setTimeout(() => {
                ele.classList.remove('blink');
            }, 3000);
        } catch (e) {
            console.error('error', e.message, e.data)
        }
      })
    </script>
    <script src="https://assets-1306445775.cos.ap-shanghai.myqcloud.com/templateA/assets/js/editor.bundle.js"></script>
    `,
    )

    return root.toString()
  }

  private updateScriptStyle(root: HTMLElement, prefix: string) {
    const links = root.querySelectorAll('link')
    links.forEach(link => {
      link.setAttribute('href', prefix + link.getAttribute('href'))
    })
    const scripts = root.querySelectorAll('script')
    scripts.forEach(script => {
      script.setAttribute('src', prefix + script.getAttribute('src'))
    })
  }

  private async getTemplate(template: string) {
    const cosRet: any = await new Promise((resolve, reject) => {
      this.cos.getObjectUrl(
        {
          Bucket: BUCKET,
          Region: 'ap-shanghai',
          Key: `${template}`,
          Sign: false,
        },
        function (err, data) {
          if (err) {
            reject(err)
          } else {
            resolve(data)
          }
        },
      )
    })
    this.logger.debug!(`url ${cosRet.Url}`)
    const prefix = cosRet.Url.replace('template.hbs', '')
    const hbsTpl = await fetch(cosRet.Url).then(res => res.text())
    const compiledTemplate = Handlebars.compile(hbsTpl)
    return { prefix, compiledTemplate }
  }

  async submitPreviewSite(dto: SubmitPreviewSiteSchemaDto) {
    const siteRet = await this.prisma.site.update({
      where: {
        id: dto.siteId,
      },
      data: {
        slotData: dto.slotData,
      },
      include: {
        siteTemplate: true,
      },
    })
    // 发 cos
    const cosUrl = await this.deploySiteToCos(siteRet.id)
    await this.prisma.site.update({
      where: {
        id: dto.siteId,
      },
      data: {
        cosUrl: cosUrl,
      },
    })
    return {
      success: true,
    }
  }

  private async deploySiteToCos(siteId: number) {
    const siteRet = await this.prisma.site.findUniqueOrThrow({
      where: {
        id: siteId,
      },
      include: {
        siteTemplate: true,
      },
    })
    const { prefix, compiledTemplate } = await this.getTemplate(siteRet.siteTemplate.template)
    const generatedHTML = compiledTemplate(Object.assign(siteRet.slotData as any, { id: siteRet.id }))
    const root = parse(generatedHTML)
    this.updateScriptStyle(root, prefix)

    const cosUrl: string = await new Promise((resolve, reject) => {
      this.cos.putObject(
        {
          Bucket: BUCKET,
          Region: 'ap-shanghai',
          Key: `${siteRet.name}/index.html`,
          StorageClass: 'STANDARD',
          Body: root.toString(),
          ContentType: 'text/html',
          onProgress: progressData => {
            this.logger.debug!(JSON.stringify(progressData))
          },
        },
        (err: any, data: any) => {
          if (err) {
            this.logger.error(`upload to tencent error: ${JSON.stringify(err || data)}`)
            reject(err)
          } else {
            resolve(data?.Location as string)
          }
        },
      )
    })
    this.logger.debug!(`Cos url: ${cosUrl}`)
    return cosUrl
  }

  async getTemplateDataDef(dto: GetTemplateDataDefSchemaDto) {
    const siteRet = await this.prisma.site.findUniqueOrThrow({
      where: {
        id: dto.siteId,
      },
      include: {
        siteTemplate: true,
      },
    })

    const defRet = await this.prisma.siteTemplateDataDef.findUniqueOrThrow({
      where: {
        siteTemplateId: siteRet.siteTemplateId,
      },
    })

    this.logger.debug!(`siteTemplate ${siteRet.siteTemplate.template}`)

    return {
      slotSchema: defRet.defData,
      slotData: siteRet.slotData,
    }
  }

  async getCustomerDef() {
    return zodToOpenAPI(CustomerDataDef)
  }

  async generateSiteJob(dto: GenerateSiteJobSchemaDto) {
    // https://github.com/prisma/prisma/issues/3888#issuecomment-709868053
    const customerRet = await this.prisma.customer.findMany({
      take: dto.take || 10,
      skip: 0,
      where: {
        OR: [
          {
            site: {
              none: {},
            },
          },
          {
            site: {
              some: {
                cosUrl: '',
              },
            },
          },
        ],
      },
    })
    const tplRet = await this.prisma.siteTemplate.findFirstOrThrow({
      where: {
        isDeleted: false,
      },
      orderBy: {
        id: 'desc',
      },
    })
    let passCnt = 0
    let failCnt = 0
    for await (const cust of customerRet) {
      try {
        const siteRet = await this.generateSite({
          customerId: cust.id,
          templateId: tplRet.id,
        })
        const cosUrl = await this.deploySiteToCos(siteRet.id)
        await this.prisma.site.update({
          where: {
            id: siteRet.id,
          },
          data: {
            cosUrl,
          },
        })
        this.logger.debug!(`[generateSiteJob] success, ${cust.name}, template: ${tplRet.name}`)
        passCnt++
      } catch (e) {
        this.logger.error(`[generateSiteJob] ${cust.name}, template: ${tplRet.template}`)
        console.error(e)
        failCnt++
      }
    }

    return {
      success: passCnt,
      fail: failCnt,
    }
  }

  async getRandomImages(dto: GetRandomImagesQuery) {
    // db.Prisma.join
    // https://github.com/prisma/prisma/issues/13663#issuecomment-1237142453
    return this.prisma
      .$queryRaw`SELECT id,unsplashId,tag,urls,css FROM ImageLibrary WHERE IsDeleted = 0 AND tag in (${db.Prisma.join(
      dto.tag.split(','),
    )}) ORDER BY RAND() LIMIT ${dto.count}`
  }
}
