import { Body, Controller, Get, HttpCode, Post, Query, Request, UseGuards } from '@nestjs/common'
import {
  CmsAdminSchemaService,
  CustomService,
  GeneratePartialSlotDataSchemaDto,
  GenerateSiteJobSchemaDto,
  GenerateSiteSchemaDto,
  GetRandomImagesQuery,
  GetTemplateDataDefSchemaDto,
  SubmitPreviewSiteSchemaDto,
} from '@flowda-projects/cms-admin-services'
import { UserJwtAuthGuard } from '../user/userJwtAuth.guard'

@Controller('/apps')
export class AppController {
  constructor(private readonly cmsAdminSchemaService: CmsAdminSchemaService, private readonly custom: CustomService) {}

  @Get('/__hi')
  async hi() {
    return {
      hi: 'cms-admin-api',
    }
  }

  @Post('/submitPreviewSite')
  async submitPreviewSite(@Body() dto: SubmitPreviewSiteSchemaDto) {
    return this.custom.submitPreviewSite(dto)
  }

  @Get('/getSchema')
  @UseGuards(UserJwtAuthGuard)
  async getSchema(@Request() req: any) {
    const schema = await this.cmsAdminSchemaService.getSchema(req.user)
    return schema
  }

  @Post('/getTemplateDataDef')
  @HttpCode(200)
  @UseGuards(UserJwtAuthGuard)
  async getTemplateDataDef(@Body() dto: GetTemplateDataDefSchemaDto) {
    return this.custom.getTemplateDataDef(dto)
  }

  @Get('/getCustomerDef')
  @HttpCode(200)
  @UseGuards(UserJwtAuthGuard)
  async getCustomerDef() {
    return this.custom.getCustomerDef()
  }

  @Get('/generateSiteJob')
  @HttpCode(200)
  @UseGuards(UserJwtAuthGuard)
  async generateJobSite(@Body() dto: GenerateSiteJobSchemaDto) {
    return this.custom.generateSiteJob(dto)
  }

  @Post('/generateSite')
  @UseGuards(UserJwtAuthGuard)
  async generateSite(@Body() dto: GenerateSiteSchemaDto) {
    return this.custom.generateSite(dto)
  }

  @Post('/generatePartialSlotData')
  @UseGuards(UserJwtAuthGuard)
  async generatePartialSlotData(@Body() dto: GeneratePartialSlotDataSchemaDto) {
    return this.custom.generatePartialSlotData(dto)
  }

  @Get('/getRandomImages')
  @UseGuards(UserJwtAuthGuard)
  async getRandomImages(@Query() query: GetRandomImagesQuery) {
    return this.custom.getRandomImages(query)
  }
}
